import React from 'react';
import Head from 'next/head';
import {Button} from 'reactstrap';

const Wrapper = props => {
    return (
        <div className={"p-3 bg-white"}>
            {props.children}
        </div>
    );
};

const Header = props => {
    return (
        <div className={"p-3 pl-5 pr-5 bg-blue"}>
            {props.children}
        </div>
    );
};

const Brand = props => {
    return (
        <div className={"p-1 bg-white rounded-circle"}><img src={"/static/images/dosenjaga.png"} height={"32"} className={"rounded-circle"}/></div>
    );
};

const Menu = props => {
    return (
        <div className={"d-flex justify-content-end flex-reverse"}>
            {props.children}
        </div>
    )
};

const MenuItem = props => {
    return (
        <div className={"rounded ml-2 pt-2 pl-4 pr-4 small text-uppercase text-white pointer font-weight-light " + (props.active ? "bg-darkblue":"")}>{props.children}</div>
    );
};

const FlexBetween = props => {
    return (
        <div className={"d-flex justify-content-between"}>{props.children}</div>
    );
}

const Divider = props => {
    return (
        <div className={`p-${props.size}`}></div>
    );
}

const Content = props => {
    return (
        <div className="h2 w-25 text-white font-weight-bold text-uppercase">{props.children}</div>
    );
}

class Index extends React.Component {
    render() {
        return (
            <div>
                <Head>
                    <title>Dosen Jaga</title>
                </Head>
                <Wrapper>
                    <Header>
                        <FlexBetween>
                            <Brand/>
                            <Menu>
                                <MenuItem active>Home</MenuItem>
                                <MenuItem>About</MenuItem>
                                <MenuItem>Help</MenuItem>
                                <MenuItem>Sign In</MenuItem>
                            </Menu>
                        </FlexBetween>
                        <Divider size={5}/>
                        <FlexBetween>
                            <Content>Pendidikan Jarak Jauh PENS.</Content>
                        </FlexBetween>
                        <Divider size={2}/>
                        <FlexBetween>
                            <div>
                                <Button className="bg-purple border-0">SIGN IN</Button>
                            </div>
                        </FlexBetween>
                    </Header>
                </Wrapper>
            </div>
        )
    }
}

export default Index;


